import { getWorkers, addComment, getWorkerById } from '../../../services/workersService';
import * as actionTypes from './actionTypes';

const _loadWorkers = () => {
    return {
        type: actionTypes.LOAD_WORKERS,
    }
}

const _loadWorkersSuccess = (data) => {
    return {
        type: actionTypes.LOAD_WORKERS_SUCCESS,
        payload: data
    }
}

const _loadWorkersFail = (error) => {
    return {
        type: actionTypes.LOAD_WORKERS_FAIL,
        payload: error
    }
}

export const loadWorkers = (criteria) => async (dispatch) => {
    dispatch(_loadWorkers())
    try {
        const { data } = await getWorkers(criteria);
        dispatch(_loadWorkersSuccess(data))
    } catch (e) {
        console.log(e);
        dispatch(_loadWorkersFail(e))
    }
};

export const updateCriteria = (criteria) => {
    return {
        type: actionTypes.UPDATE_CRITERIA,
        payload: criteria
    }
}

export const updateInitialPage = (selectedPage) => {
    return {
        type: actionTypes.UPDATE_INITIAL_PAGE,
        payload: selectedPage
    }
}

export const clearDataWorkers = () => ({ type: actionTypes.CLEAR_DATA_WORKERS });

const _addComment = (comments) => {
    return {
        type: actionTypes.ADD_COMMENT,
        payload: comments
    }
};

export const addCommentHandler = (dataComment, workerId) => async dispatch => {
    try {
        const { data } = await addComment(dataComment, workerId);
        debugger;
        dispatch(_addComment(data.comments));
    } catch (err) {
        throw new Error('Error on add new comment', err);
    }
};

// Single worker 

const _loadWorker = () => {
    return {
        type: actionTypes.LOAD_WORKER
    }
};

const _loadWorkerSuccess = (data) => {
    return {
        type: actionTypes.LOAD_WORKER_SUCCESS,
        payload: data
    }
}

const _loadWorkerFail = (error) => {
    return {
        type: actionTypes.LOAD_WORKER_FAIL,
        payload: error
    }
};



export const loadWorkerById = (id) => async dispatch => {
    dispatch(_loadWorker());
    try {
        const { data } = await getWorkerById(id);
        if (data) {
            dispatch(_loadWorkerSuccess(data))
        }
    } catch (err) {
        dispatch(dispatch(_loadWorkerFail(err)));
        throw new Error(err);
    }
};