import React, { useState, useEffect, useCallback } from 'react';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import { Paper, Box, Button, CircularProgress } from '@material-ui/core';
import styles from './Login.module.scss';
import { ValidatorForm, TextValidator } from 'react-material-ui-form-validator';
import { loginUser, getUser } from '../../services/userService';
import { useDispatch, useSelector } from 'react-redux';
import * as actionTypes from '../../store/actions/actionsUser/actionTypes';
import { useHistory } from 'react-router-dom';
import { store } from '../../services/StorageService';
import { getUserLoggedIn } from '../../services/authService';

const theme = createMuiTheme({
    direction: 'rtl'
});


const Login = (props) => {

    const [userInfo, setUserInfo] = useState({ email: '', password: '' });
    const [isProgress, setIsProgress] = useState(false);
    const [isErrorUserInfo, setisErrorUserInfo] = useState(false);
    const userInfoState = useSelector(state => state.userReducer.userInfo);
    const lastPathName = useSelector(state => state.routerReducer.lastPathName);
    const [isFinished, setIsFininshed] = useState(false);

    const dispatch = useDispatch();
    const history = useHistory();

    useEffect(() => {
        if (history.action === 'POP') { // menually
            checkIsLoggedIn()
        }
    }, [])

    const checkIsLoggedIn = useCallback(async () => {
        setIsFininshed(false);
        try {
            const data = await getUserLoggedIn();
            setIsFininshed(true);
            console.log('data', data);
            if (data) {
                if (lastPathName) {
                    history.push(lastPathName)
                } else {
                    history.push('/')
                }
            }
        } catch (e) {
            setIsFininshed(true);
        }
    }, [lastPathName, history]);


    useEffect(() => {
        if (history.action === 'PUSH') {
            setIsFininshed(true);
            if (userInfoState && userInfoState.id) {
                if (lastPathName) {
                    history.push(lastPathName)
                } else {
                    history.replace('/')
                }
            }
        }
    }, [userInfoState, history])


    useEffect(() => {
        ValidatorForm.addValidationRule('minLength', (value) => {
            if (value.length < 6) {
                return false
            };
            return true;
        })
        return () => {
            ValidatorForm.removeValidationRule('minLength')
        }
    }, []);


    const onFormChanged = (name, value) => {
        setUserInfo(prevState => {
            return {
                ...prevState,
                [name]: value
            }
        });
    }

    const submitForm = useCallback(async (e) => {
        e.preventDefault()
        setIsProgress(true);
        setisErrorUserInfo(false);
        const { data } = await loginUser(userInfo);
        if (data) {
            setIsProgress(false);
            dispatch({ type: actionTypes.SET_USER, payload: data });
            store('uid', data.id);
            history.push('/')
        } else {
            setisErrorUserInfo(true);
        }
    }, [history, userInfo]);


    return (
        <>
            {isFinished ? <MuiThemeProvider theme={theme}>
                <ValidatorForm onSubmit={submitForm} onError={errors => console.log(errors)}>
                    <Box classes={{ root: styles.boxPage }} display="flex" alignItems="center">
                        <Paper classes={{ root: styles.paperLogin }} elevation={3}>
                            <TextValidator
                                validators={['required', 'isEmail']}
                                errorMessages={['שדה חובה', 'אימייל לא תקין']}
                                name="email"
                                value={userInfo.email}
                                classes={{ root: styles.inputStyle }}
                                label="אימייל"
                                onChange={(e) => onFormChanged('email', e.target.value)}
                                autoComplete='off' />
                            <TextValidator
                                classes={{ root: styles.inputStyle }}
                                label="סיסמא"
                                onChange={(e) => onFormChanged('password', e.target.value)}
                                minLength={6}
                                validators={['required', 'minLength']}
                                errorMessages={['שדה חובה', 'מינימום 6 תווים']}
                                type="password"
                                value={userInfo.password}
                                autoComplete='off' />
                            <div className={styles.sectionBottom}>
                                {isErrorUserInfo ? <span className={styles.incorrectInfo}>שם משתמש או סיסמא לא נכונים</span> : null}
                                <Button type="submit" variant="contained" color="secondary" classes={{ root: styles.btnSubmit }}>
                                    <span>כניסה</span>
                                    {isProgress && !isErrorUserInfo? <CircularProgress thickness={5.5} classes={{ root: styles.circularPrrogress }} size={18} /> : null}
                                </Button>
                            </div>
                        </Paper>
                    </Box>
                </ValidatorForm>
            </MuiThemeProvider> : null}
        </>
    )
}

export default Login;
