import React, { useEffect, useState } from 'react';
import './App.css';
import { Route, useHistory } from 'react-router-dom';
import { WorkersPage } from './pages/workers/WorkersPage/WorkersPage';
import { Home } from './pages/Home/Home';
import WorkerDetails from './pages/workers/WorkerDetails/WorkerDetails';
import Login from './pages/Login/Login';
import { getUser, logoutUser } from './services/userService';
import { useDispatch, useSelector } from 'react-redux';
import * as actionTypes from './store/actions/actionsUser/actionTypes';
import { load, store, clearKey } from './services/StorageService';

const App = () => {
  // const [userDetails, setUserDetails] = useState();
  const userInfoState = useSelector(state => state.userReducer.userInfo);
  const dispatch = useDispatch();
  const history = useHistory();

  useEffect(() => {
    console.log('INIT APP');
    checkIsLoggedIn()
  }, [dispatch]);

  const checkIsLoggedIn = async () => {
    const id = load('uid');
    if (!id) return;
    try {
      const { data } = await getUser(id);
      console.log('currentuser', data);
      if (data) {
        dispatch({ type: actionTypes.SET_USER, payload: data })
      }
    } catch (e) {
      console.log('e', e);
    }
  };

  const logout = () => {
    clearKey('uid');
    dispatch({ type: actionTypes.CLEAR_USER });
    history.push('/login');
  };

  const goToLoginPage = () => {
    history.push('/login');
  };

  return (
    <section>
      <header className="navbar-app">
        <div>מאגר הבייביסיטר הגדול בישראל</div>
        {userInfoState && userInfoState.id ? <button onClick={logout}>התנתק</button> : <button onClick={goToLoginPage}>התחבר</button>}
      </header>
      <Route path="/" exact component={Home} />
      <Route path="/login" exact component={Login} />
      <Route path="/workers/:cityId" exact component={WorkersPage} />
      <Route path="/workers/:cityId/:workerId" exact component={WorkerDetails} />
    </section>
  );
}

export default App;
