import React, { useEffect, useState, useRef } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { WorkersList } from '../../../components/workers/WorkersList/WorkersList';
import * as actions from '../../../store/actions/index';
import { useParams } from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import styles from './WorkersPage.module.scss';
import BabysitterActionsBtns from '../../../components/BabysitterActionsBtns/SearchBabysitter';
import ReactPaginate from 'react-paginate';
import KeyboardArrowRightOutlinedIcon from '@material-ui/icons/KeyboardArrowRightOutlined';
import KeyboardArrowLeftOutlinedIcon from '@material-ui/icons/KeyboardArrowLeftOutlined';

export const WorkersPage = () => {
    const workers = useSelector(state => state.workersReducer.workers);
    const isLoading = useSelector(state => state.workersReducer.isLoading);
    const isFinished = useSelector(state => state.workersReducer.isFinished);
    const criteria = useSelector(state => state.workersReducer.criteria);
    const initialPage = useSelector(state => state.workersReducer.initialPage);
    const count = useSelector(state => state.workersReducer.count);
    const [pageNumber, setPageNumber] = useState(0);
    const [pageCount, setPageCount] = useState(0);
    const isInitial = useRef(true);
    const dispatch = useDispatch();
    let params = useParams();

    useEffect(() => {
        const pageCount = Math.ceil(count / criteria.pageSize);
        setPageCount(pageCount);
    }, [count]);

    useEffect(() => {
        console.log('paginate');
        const reqCriteria = { ...criteria, cityId: params.cityId };
        dispatch(actions.loadWorkers(reqCriteria))

    }, [dispatch, params, criteria]);

    useEffect(() => {
        if (!isInitial.current) {
            let offset = Math.ceil(pageNumber * criteria.pageSize);
            const requestedCriteria = { ...criteria, offset };
            console.log('requestedCriteria', requestedCriteria);
            dispatch(actions.updateCriteria(requestedCriteria));
            dispatch(actions.updateInitialPage(pageNumber));
        } else {
            isInitial.current = false;
        }
    }, [pageNumber, dispatch])


    return (
        <>
            {isLoading ?
                <div className={styles.containerSpinner}><CircularProgress thickness={6.5} size={50} /></div>
                :
                <>
                    {isFinished ?
                        <div className="container">
                            <div className={styles.workersPageListContainer}>
                                <BabysitterActionsBtns />
                                <WorkersList workersList={workers} />
                            </div>
                            <ReactPaginate
                                previousLabel={<KeyboardArrowRightOutlinedIcon />}
                                nextLabel={<KeyboardArrowLeftOutlinedIcon />}
                                breakLabel={'...'}
                                breakClassName={'break-me'}
                                pageCount={pageCount}
                                marginPagesDisplayed={0}
                                pageRangeDisplayed={5}
                                forcePage={pageNumber}
                                initialPage={initialPage}
                                onPageChange={({ selected }) => setPageNumber(selected)}
                                containerClassName={'pagination'}
                                subContainerClassName={'pages pagination'}
                                activeClassName={'active'}
                            />
                        </div> : null}
                </>
            }
        </>
    )
}
