import * as actionTypes from '../../actions/actionsWorkers/actionTypes';
const initialState = {
    worker: null,
    isLoading: false,
    isFinished: false, // handle for CircularProgress (need to know when is finished)
    error: null,

};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.LOAD_WORKER:
            return {
                ...state,
                isLoading: true,
                isFinished: false
            }
        case actionTypes.LOAD_WORKER_SUCCESS:
            return {
                ...state,
                isLoading: false,
                isFinished: true,
                worker: action.payload,
            }
        case actionTypes.LOAD_WORKER_FAIL:
            return {
                ...state,
                error: action.payload,
                isFinished: true,
                isLoading: false
            }
        default:
            return state;
    }
};

export default reducer;