import axios from 'axios';
const LOGIN_URL = (process.env.NODE_ENV !== 'development')
    ? '/login'
    : '//localhost:8080/login';

const USER_URL = (process.env.NODE_ENV !== 'development')
    ? '/user'
    : '//localhost:8080/user';

const LOGOUT_URL = (process.env.NODE_ENV !== 'development')
    ? '/logout'
    : '//localhost:8080/logout';

export const loginUser = async (userInfo) => {
    return await axios.post(LOGIN_URL, userInfo, { withCredentials: true });
};


export const getUser = async (id) => {
    // return await axios.get(USER_URL, { withCredentials: true });
    return await axios.post(USER_URL, { id });
};

export const logoutUser = async () => {
    return await axios.get(LOGOUT_URL);
};

export const insertActivate = async (userId, workerId, infoActivates) => {
    return await axios.post(`${USER_URL}/insert-activate`, { userId, workerId, infoActivates })
};

export const updateActivate = async (userId, workerId, infoActivates) => {
    return await axios.put(`${USER_URL}/update-activate`, { userId, workerId, infoActivates })
};

