import axios from 'axios';
const CITIES_URL = (process.env.NODE_ENV !== 'development')
    ? '/cities'
    : '//localhost:8080/cities';

export const getCities = async (term) => {
    return await axios.post(CITIES_URL, { name: term })
};

export const getCitiesDemo = async (offest) => {
    return await axios.post(`${CITIES_URL}/demo`, { offest });
}
