import * as actionTypes from '../../actions/actionsWorkers/actionTypes';
import clone from 'clone';
const initialState = {
    workers: [],
    isLoading: false,
    isFinished: false,
    error: null,
    criteria: {
        offset: 0,
        pageSize: 3,
    },
    initialPage: 0,
    count: null
};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.LOAD_WORKERS:
            return {
                ...state,
                isLoading: true
            }
        case actionTypes.LOAD_WORKERS_SUCCESS:
            return {
                ...state,
                workers: action.payload.workers,
                isLoading: false,
                isFinished: true,
                count: action.payload.count
            }
        case actionTypes.LOAD_WORKERS_FAIL:
            return {
                ...state,
                isLoading: false,
                error: action.payload,
                isFinished: true
            }
        case actionTypes.UPDATE_CRITERIA:
            return {
                ...state,
                criteria: action.payload
            }
        case actionTypes.UPDATE_INITIAL_PAGE:
            return {
                ...state,
                initialPage: action.payload
            }
        case actionTypes.CLEAR_DATA_WORKERS:
            return initialState;
        default:
            return state;
    }
};

export default reducer;