import React, { useState, useMemo, useEffect, useCallback } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import styles from './WorkerDetails.module.scss';
import BabysitterActionsBtns from '../../../components/BabysitterActionsBtns/SearchBabysitter';
import { Typography, Button } from '@material-ui/core';
import Rating from '@material-ui/lab/Rating';
import RatingDialog from '../../../components/RatingDialog/RatingDialog'
import { useDispatch, useSelector } from 'react-redux';
import * as actionTypesRouter from '../../../store/actions/actionsRouter/actionTypes/actionTypes';
import { useHistory, Link, useRouteMatch } from 'react-router-dom';
import { updateRating, getWorkerById } from '../../../services/workersService';
import CircularProgress from '@material-ui/core/CircularProgress';
import clone from 'clone';
import { setActivates, onInsertActivate, onUpdateActivate } from '../../../store/actions/actionsUser/users';
import CommentsContainer from '../../../components/workers/CommentsContainer/CommentsContainer';
import { addCommentHandler } from '../../../store/actions';
import * as actions from '../../../store/actions/index';


const WorkerDetails = (props) => {
    const userInfoState = useSelector(state => state.userReducer.userInfo);
    const workerState = useSelector(state => state.workerReducer.worker);
    const isLoading = useSelector(state => state.workerReducer.isLoading);
    const isFinished = useSelector(state => state.workerReducer.isFinished);
    const dispatch = useDispatch();
    const history = useHistory();
    const match = useRouteMatch();

    useEffect(() => {
        dispatch({ type: actionTypesRouter.SET_LAST_PATH_NAME, payload: history.location.pathname });
        dispatch(actions.loadWorkerById(match.params.workerId));
    }, []);

    const onAddComment = (comment) => {
        const data = {
            comment,
            userId: userInfoState.id,
            userFirstName: userInfoState.firstName,
            userLastName: userInfoState.lastName
        };
        // dispatch(addCommentHandler(data, worker._id));
    }

    return (
        <>
            {isLoading ?
                <div className={styles.containerSpinner}><CircularProgress thickness={6.5} size={50} /></div>
                :
                <>
                    {isFinished && <div className="container">
                        <BabysitterActionsBtns />
                        <div className={styles.containerDetailsPerson}>
                            <Card classes={{ root: styles.card }}>
                                <CardContent classes={{ root: styles.cardContent }}>
                                    <div className="flex space-between ">
                                        <div className={styles.personalDetails}>
                                            <Typography classes={{ h5: styles.title }} gutterBottom={true} variant="h5">פרטים אישיים</Typography>
                                            <div className={styles.fieldRow}>
                                                <span>שם</span>
                                                <span>נאור מרדכי</span>
                                            </div>
                                            <div className={styles.fieldRow}>
                                                <span>גיל</span>
                                                <span>24</span>
                                            </div>
                                            <div className={styles.fieldRow}>
                                                <span>עיר/יישוב</span>
                                                <span>באר שבע</span>
                                            </div>
                                            <div className={styles.fieldRow}>
                                                <span>מצב משפחתי</span>
                                                <span>רווק/ה</span>
                                            </div>
                                            <div className={styles.fieldRow}>
                                                <span>בעל/ת רישיון נהיגה</span>
                                                <span>כן</span>
                                            </div>
                                            <div className={styles.fieldRow}>
                                                <span>עדכון אחרון</span>
                                                <span>1/1/2020</span>
                                            </div>
                                            <div className={styles.fieldRow}>
                                                <span>מס' טלפון</span>
                                                {userInfoState && userInfoState.id ?
                                                    <span>0523213211</span> :
                                                    <>
                                                        <span>הירשמו ותוכלו לראות את פרטי ההתקשרות</span>
                                                        <Link to="/login"><Button variant="contained" color="secondary">התחבר</Button></Link>
                                                    </>}
                                            </div>
                                        </div>
                                        <div className={styles.boxImage}>
                                            <img src="http://tecnosisweb.com.br/assets/pages/img/people/img3-large.jpg" />
                                        </div>
                                    </div>
                                    <div className={styles.descriptionExperienceBox}>
                                        <Typography classes={{ h5: styles.title }} gutterBottom={true} variant="h5">תאור ניסיון</Typography>
                                        <div>
                                            ניסיון מעל 10 שנים עם תינוקות מגיל 0 עד 2, ילדים בגילאים בין 2 ל 6, ילדים מעל גיל 6
                                            עבדתי במערכת החינוך בקיבוץ בו גדלתי, שם עבדתי בכל בתי הילדים מהגיל הרך ועד ילדי בית הספר.
                                            למדתי חינוך ואמנות במכללת סמינר הקיבוצים ותוך כדי גם לימדתי אמנות נערים ונערות בחטיבת הביניים.
                                            כעת אני עובדת בגן ילדים פרטי בת"א עם ילדים בני שנתיים עד 4
                                            ולומדת תואר שני בטיפול באמנות.
                        </div>
                                    </div>
                                    <div className={styles.infoAboutJob}>
                                        <Typography classes={{ h5: styles.title }} gutterBottom={true} variant="h5">מידע על המשרה</Typography>
                                        <div className={styles.fieldRow}>
                                            <span>ימי עבודה</span>
                                            <span>ראשון, שני, רביעי, חמישי</span>
                                        </div>
                                        <div className={styles.fieldRow}>
                                            <span>גילאים מועדפים</span>
                                            <span>תינוקות בגילאים 0-2, ילדים בגילאים 2-6</span>
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>
                            <div className={styles.commentsContainer}>
                                <CommentsContainer addCommentHandler={onAddComment} />
                            </div>
                        </div>
                    </div>
                    }
                </>
            }
        </>
    )
}

export default WorkerDetails
