import React, { useRef } from 'react'
import TextField from '@material-ui/core/TextField';
import Avatar from '@material-ui/core/Avatar';
import styles from './CommentsContainer.module.scss'
import Button from '@material-ui/core/Button';


const CommentsContainer = ({ addCommentHandler }) => {
    let inputEl = useRef();
    const onAddCommentHandler = (ev) => {
        ev.preventDefault();
        if (inputEl.value.length) {
            addCommentHandler(inputEl.value);
        }

    };
    return (
        <div>
            <form onSubmit={ev => onAddCommentHandler(ev)}>
                <section className={styles.sectionAdditionalComment}>
                    <Avatar />
                    <TextField
                        placeholder="תגובה חדשה..."
                        multiline
                        rowsMax="4"
                        inputRef={e => inputEl = e}
                    />
                </section>
                <div className={styles.btnsActions}>
                    <Button variant="outlined" color="primary" size="small" type="submit">
                        ביטול
                    </Button>
                    <Button variant="contained" color="primary" size="small">
                        תגובה
                    </Button>
                </div>
            </form>
        </div>
    )
}

export default CommentsContainer;
