import { load } from "./StorageService";
import { getUser } from './userService';

export const getUserLoggedIn = async () => {
    const id = load('uid');
    if (id) {
        const user = await getUser(id);
        console.log('user', user);
        
        if (user) {
            return user;
        } else {
            return null;
        }
    } else {
        return null;
    }
};