import React, { useState, useCallback } from 'react';
import Button from '@material-ui/core/Button';
import styles from './SearchBabysitter.module.scss'
import { CitiesAutocomplete } from '../../shared/components/CitiesAutocomplete';
import { withRouter, useHistory } from 'react-router-dom';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import { SEARCH_BABYSITTER, SEARCH_BABYSITTER_JOB } from '../../constans/typeOfSearchJob';
import { useDispatch } from 'react-redux';
import * as actions from '../../store/actions/index';

const BabysitterActionsBtns = () => {
    const [jobType, setJobType] = useState('');
    const [open, setOpen] = useState(false);
    const [selectedCity, setSelectedCity] = useState();
    const [textValue, setTextValue] = useState('');

    const history = useHistory();
    const dispatch = useDispatch();

    const search = useCallback(() => {
        if (jobType === SEARCH_BABYSITTER) {
            history.push(`/workers/${selectedCity._id}`);
            dispatch(actions.clearDataWorkers());
        };
    }, [history, jobType, selectedCity, dispatch]);

    const handlerTypeOptionSearch = (type) => {
        setJobType(type);
        handleClickOpen()
    };

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
        setSelectedCity(null)
    };


    return (
        <>
            <div className={`flex justify-content-center ${styles.container}`}>
                <Button variant="contained" color="primary" onClick={() => handlerTypeOptionSearch(SEARCH_BABYSITTER)}>
                    מחפש/ת בייביסיטר
            </Button>
                <Button variant="contained" color="primary" onClick={() => handlerTypeOptionSearch(SEARCH_BABYSITTER_JOB)}>
                    מחפש/ת עבודה בתור בייביסיטר
            </Button>
            </div>
            <Dialog open={open} onClose={handleClose} fullWidth={true}>
                <DialogTitle id="form-dialog-title">ערים לבחירה</DialogTitle>
                <DialogContent className="flex space-between full-width">
                    <CitiesAutocomplete
                        setSelectedCity={setSelectedCity}
                        setTextValue={setTextValue}
                        styleAutocomplete="autocmplete-style-dialog" />
                    <Button onClick={search} variant="contained" color="primary" disabled={!selectedCity || textValue !== selectedCity.name}>
                        חיפוש
                    </Button>
                </DialogContent>
            </Dialog>

        </>
    )
};

export default withRouter(BabysitterActionsBtns);