import React from 'react';

import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Rating from '@material-ui/lab/Rating';
import Box from '@material-ui/core/Box';
import styles from './WorkerRow.module.scss'
import { withRouter, useHistory, useLocation } from 'react-router-dom';

const WorkerRow = (props) => {

    const history = useHistory();
    const location = useLocation();

    const goToDetails = () => {
        history.push(`${location.pathname}/${props.worker._id}`)
    };

    return (
        <div className={styles.cardContainer}>
            <span className={styles.dateText}>תאריך עדכון: 12/12/2019</span>
            <Card classes={{ root: styles.card }}>
                <CardContent className="flex" classes={{ root: styles.cardContent }}>
                    <div className={styles.personalInfoRight}>
                        <Box className={styles.cardImg}>
                            <img alt="worker" onClick={goToDetails} className={styles.mainimgPerson} src="http://tecnosisweb.com.br/assets/pages/img/people/img3-large.jpg" />
                            <Rating classes={{root:styles.rating}} name="size-small" value={2} size="small" readOnly />
                        </Box>
                        <div className={styles.personalDetailsRight}>
                            <div>
                                <span className="text-opacity">שם מלא:&nbsp;</span>
                                <span>רמי לוי</span>
                            </div>
                            <div>
                                <span className="text-opacity">גיל:&nbsp;</span>
                                <span>35</span>
                            </div>
                            <div>
                                <span className="text-opacity">מקום מגורים:&nbsp;</span>
                                <span>באר שבע</span>
                            </div>
                            <div>
                                <span className="text-opacity">ניסיון:&nbsp;</span>
                                <span>4 שנים</span>
                            </div>
                        </div>
                    </div>
                    <div className={styles.personalInfoLeft}>
                        <div>
                            <span className="text-opacity">גילאים מועדפים:&nbsp;</span>
                            <span>תינוקות מגילאים 0 עד 2</span>
                        </div>
                        <div className={styles.mySelfDescBox}>
                            <span><span className="text-opacity">קצת על עצמי:</span> Lorem Ipsum is simply dummy text of the printing and typesetting industry.
                                Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an
                                unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages,
                             and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</span>
                        </div>
                        <div className={styles.workDays}>
                            <span className="text-opacity">ימי עבודה: &nbsp;</span>
                            <span>ראשון &#8226; שני &#8226; שלישי &#8226; רביעי &#8226; חמישי</span>
                        </div>
                    </div>
                </CardContent>
                <CardActions classes={{ root: styles.cardActions }}>
                    <Button variant="contained" onClick={goToDetails} size="small">קרא עוד</Button>
                </CardActions>
            </Card>
        </div>
    )
}

export default withRouter(WorkerRow);