const _onInsertActivate = (data) => {
    return {
        type: actionTypes.INSERT_ACTIVATE,
        payload: data
    }
};

export const onInsertActivate = (userId, workerId, infoActivates) => async (dispatch) => {
    try {
        const { data } = await insertActivate(userId, workerId, infoActivates); //boolean
        if (data) {
            const insertedActivate = {
                [workerId]: { ...infoActivates }
            };
            dispatch(_onInsertActivate(insertedActivate))
        }
        debugger;
    } catch (e) {
        console.log('error insert activate', e)
    }

};

const _onUpdateActivate = (data) => {
    return {
        type: actionTypes.UPDATE_USER_ACTIVATES,
        payload: data
    }
};

export const onUpdateActivate = (userId, workerId, infoActivates) => async (dispatch) => {
    try {
        const { data } = await updateActivate(userId, workerId, infoActivates);
        debugger;
    } catch (err) {
        console.log('ERROR OCCUERD IN UPDATE ACTIVATE', err);
    }
};