import * as actionTypes from '../../actions/actionsUser/actionTypes';

const initialState = {
    userInfo: {
        firstName: '',
        lastName: '',
        id: '',
        activates: []
    }
};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_USER:
            return {
                ...state,
                userInfo: action.payload
            }
        case actionTypes.INSERT_ACTIVATE:
            return {
                ...state,
                userInfo: {
                    ...state.userInfo,
                    activates: state.userInfo.activates.concat(action.payload)
                }
            }

        case actionTypes.CLEAR_USER:
            return initialState;
        default:
            return state;
    }
};

export default reducer;