import * as actionTypes from '../../actions/actionsRouter/actionTypes/actionTypes';
const initialState = {
    lastPathName: ''
};


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_LAST_PATH_NAME:
            return {
                ...state,
                lastPathName: action.payload
            }

        default:
            return state;
    }
};

export default reducer;