import React, { useEffect, useState } from 'react'
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useSearch } from '../../hooks/useSearchStarwarsHero';
import { getCities } from '../../services/citiesService';
// get cities for autocomplete
const getCitiesSearch = async (text, abortSignal) => {
  try {
    const { data } = await getCities(text);
    return data;
  } catch (e) {
    console.log('e', e);
  }
};


export const CitiesAutocomplete = (props) => {
  const [open, setOpen] = useState(false);
  const [options, setOptions] = useState([]);
  const loading = open && options.length === 0;
  const { inputText, setInputText, search } = useSearch(getCitiesSearch);


  useEffect(() => {
    let active = true;
    if (!loading) {
      return undefined;
    }
    return () => {
      active = false;
    };
  }, [loading]);


  useEffect(() => {
    if (!open) {
      setOptions([]);
    }
  }, [open]);

  const handlerSelecetdOption = (ev, val) => {
    props.setSelectedCity(val)
  };

  const handleValueChange = (ev, val) => {
    setInputText(val);
    props.setTextValue(val)
  };

  return (
    <div className={props.styleAutocomplete}>
      <Autocomplete
        id="asynchronous-demo"
        inputValue={inputText}
        style={{ width: '100%' }}
        onInputChange={handleValueChange}
        open={open}
        onChange={handlerSelecetdOption}
        onOpen={() => {
          setOpen(true);
        }}
        onClose={() => {
          setOpen(false);
        }}
        getOptionSelected={(option, value) => option.name === value.name}
        getOptionLabel={option => option.name}
        options={search.result}
        loading={loading}
        renderInput={params => (
          <TextField
            autoFocus
            placeholder="הקלד שם עיר..."
            {...params}
            fullWidth
            variant="outlined"
            InputProps={{
              ...params.InputProps,
              endAdornment: (
                <React.Fragment>
                  {loading ? <CircularProgress color="inherit" size={20} /> : null}
                  {params.InputProps.endAdornment}
                </React.Fragment>
              ),
            }}
          />
        )}
      />

    </div>
  )
}